<template>
  <div class="container">
    <!--  style="margin-top -->
    <div class="buttons">
      <button class="button" :class="{ 'is-active': isBarActive_GDP_HG }" @click="drawBarChart_GDP_HG"
        style="margin-top: 50px;"></button>
      <button class="button" :class="{ 'is-active': isLineActive_GDP_HG }" @click="drawLineChart_GDP_HG"
        style="margin-top: 50px;"></button>
    </div>
    <div class="chart-container" id="gdp_hg"></div>

    <!--  style="margin-top -->
    <div class="buttons">
      <button class="button" :class="{ 'is-active': isBarActive_GDP_CS }" @click="drawBarChart_GDP_CS"
        style="margin-top: 50px;"></button>
      <button class="button" :class="{ 'is-active': isLineActive_GDP_CS }" @click="drawLineChart_GDP_CS"
        style="margin-top: 50px;"></button>
    </div>
    <div class="chart-container" id="gdp_cs"></div>
  </div>
</template>

<script>

import { params_gdp, sendRequest, drawCommonChart } from './CommonUtil';

export default {

  data() {
    return {
      EChartType_GDP: {
        GH: 'gdp_hg',
        GC: 'gdp_cs',
      },

      isBarActive_GDP_HG: false,
      isLineActive_GDP_HG: false,
      isBarActive_GDP_CS: false,
      isLineActive_GDP_CS: false,
      dataListHG: null,
      dataListCS: null,
      chartType: null
    };
  },
  mounted() {
    this.loadData();
  },

  methods: {
    loadData() {
      if (process.env.VUE_APP_REQUEST_IS_LOCAL === 'true') {
        this.requestWithLocalJson()
      } else {
        this.requestWithAPI()
      }
    },
    requestWithLocalJson() {
      // GDP
      fetch('gdp.json')
        .then(response => response.json())
        .then(data => {
          console.log('GDP:', data);
          // 
          this.returnData = data;
          // 
          this.drawChartWithBtn()
        })
        .catch(error => {
          console.error('Error fetching data:', error)
        })
    },
    async requestWithAPI() {
      try {
        this.returnData = await sendRequest(params_gdp);
        console.log("", this.returnData)
        this.drawChartWithBtn()
      } catch (error) {
        console.error(':', error);
      }
    },
    drawChartWithBtn() {
      if (this.returnData) {
        this.drawBarChart_GDP_HG();
        this.drawBarChart_GDP_CS();
      }
    },

    drawBarChart_GDP_HG() {
      this.isBarActive_GDP_HG = true;
      this.isLineActive_GDP_HG = false;
      // 
      this.chartType = "bar"
      this.drawChartWithParams(this.EChartType_GDP.GH)

    },
    drawLineChart_GDP_HG() {
      this.isBarActive_GDP_HG = false;
      this.isLineActive_GDP_HG = true;
      // 
      this.chartType = "line"
      this.drawChartWithParams(this.EChartType_GDP.GH)
    },
    drawBarChart_GDP_CS() {
      this.isBarActive_GDP_CS = true;
      this.isLineActive_GDP_CS = false;
      // 
      this.chartType = "bar"
      this.drawChartWithParams(this.EChartType_GDP.GC)

    },
    drawLineChart_GDP_CS() {
      this.isBarActive_GDP_CS = false;
      this.isLineActive_GDP_CS = true;
      // 
      this.chartType = "line"
      this.drawChartWithParams(this.EChartType_GDP.GC)
    },
    drawChartWithParams(echrtId) {
      // basicParams-echrtIdtitlelegendTopgridTopxAxisDataArr
      let basicParams = {};
      let typeArr = [];
      let cityCodeArr = [];
      switch (echrtId) {
        case this.EChartType_GDP.GH:
          basicParams = { echrtId: echrtId, chartType: this.chartType, title: '', subtitle: '', exceptName: '', unit: '()', legendTop: '10%', gridTop: '25%', sj: '0' }
          // A020102-CPI  A020103- A020104-  A020105-
          typeArr = ['A020102', 'A020103', 'A020104', 'A020105'];
          break;
        case this.EChartType_GDP.GC:
          basicParams = { echrtId: echrtId, chartType: this.chartType, title: '', subtitle: '', exceptName: '', unit: '()', legendTop: '10%', gridTop: '25%', sj: '0' }
          // A0101-
          typeArr = ['A0101'];
          // 110000- 440100- 440100- 440300- 330100- 510100- 420100- 320100-
          // 500000- 610100- 410100- 340100- 420500-
          cityCodeArr = ['110000', '310000', '440100', '440300', '330100', '510100', '420100', '320100', '500000', '610100', '410100', '340100', '420500']
          break;
        default:
          break;
      }
      drawCommonChart(basicParams, typeArr, this.returnData, cityCodeArr)
    }
  }
};
</script>
